import {
    ButtonBlock
} from "@webx/forms"

import {
    SettingsIcon,
    InfoIcon, 
    ContrastIcon, 
    PersonIcon
} from "icons/settings/menu"

import Header from "./Header"
import MenuButton from "./MenuButton"

export default function Menu() {
    return <div className="settings-menu">
        <Header />
        <ButtonBlock className="settings-block">
            <MenuButton 
                    icon={<ContrastIcon/>}
                    title="" 
                    page={1}
            />
            <MenuButton
                    icon={<SettingsIcon/>}
                    title=""
                    page={4}
            />
        </ButtonBlock>
        <ButtonBlock className="settings-block">
            <MenuButton 
                    icon={<InfoIcon/>} 
                    title=" " 
                    page={2}
            />
            <MenuButton 
                    icon={<PersonIcon/>} 
                    title="" 
                    page={3}
            />
        </ButtonBlock>
    </div>
}
