import {
    CardBlock
} from "@webx/forms"

import Headline from "../components/Headline"
import LinkButton from "../components/LinkButton"

export default function VKBotKit () {
    return <div className="repos-wrapper">
        <Headline title="VKBotKit" />
        <CardBlock className="repos-page options">
            <div className="repo-description">
                VKBotKit  Python ,    -   .
            </div>
            <div className="repo-description">
                VKBotKit          .      VK Bots API,    aiohttp.
            </div>
        </CardBlock>
        <CardBlock className="repos-page options">
            <div className="options-button-list">
                <LinkButton title="Python " link="https://github.com/vkbotkit/vkbotkit" />
                <LinkButton title="" link="https://github.com/vkbotkit/template" />
                <LinkButton title="" link="https://github.com/vkbotkit/examples" />
            </div>
        </CardBlock>
    </div>
}