export default function Technical () {
    return <div className="settings-wrapper">
        <Headline title="" />
        <CardBlock className="settings-page options">
            <div className="options-grid-list settings-block">
                <div className="options-grid-item">
                     
                </div>
                <div className="options-grid-item">
                    <Theme />
                </div>
                <div className="options-grid-item">
                    Offline 
                </div>
                <div className="options-grid-item">
                    <CacheApp />
                </div>
                <div className="options-grid-item">
                     offline 
                </div>
                <div className="options-grid-item">
                    <CheckForUpdates />
                </div>
            </div>
        </CardBlock>
    </div>
}