import {
    Button,
    CardBlock
} from "@webx/forms"

import "./stylesheet.scss"

function NotFound() {
    function MoveToRootButton() {

        function onClick() {
            window.location.href="https://kensoi.github.io/"
        }

        return <Button 
                title="  "
                theme="white"
                onClick={onClick}
        />
    }

    return <div className="not-found">
        <h1>
             ?
        </h1>
        <CardBlock>
            <p>
                  ,    .
            </p>
            <p>
                    .
            </p>
        </CardBlock>
        <MoveToRootButton/>
    </div>
}

export default NotFound